<template>
  <div >
    <div class="input-group"> <span class="input-group-addon">Поиск</span>
      <input id="filter" type="text" class="form-control" placeholder="Поиск" v-model="search">
    </div>
    <table class="table table-striped table-bordered">
      <thead>
        <tr>
          <th scope="col" data-param-name="equipment_type">Тип</th>
          <th scope="col" data-param-name="manufacturer" @click="sort_col">Производитель</th>
          <th scope="col" data-param-name="name" @click="sort_col">Наименование оборудования</th>
          <th scope="col" data-param-name="price" @click="sort_col">Стоимость оборудования</th>
          <th scope="col" data-param-name="owner" @click="sort_col">Принадлежность оборудования</th>
          <th scope="col" data-param-name="belonging" @click="sort_col">Закрепление оборудования за сотрудниками</th>
          <th scope="col" data-param-name="number" @click="sort_col">Накладной номер оборудования</th>
          <th scope="col" data-param-name="date_in" @click="sort_col">Дата получения оборудования</th>
          <th scope="col" data-param-name="date_out" @click="sort_col">Дата списания/выдачи оборудования из ремонта</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="element in searchResults" :key=element.id :id="element.id">
          <td @dblclick="onclick_" @blur="onblur_" @input="onInput" data-param-name="equipment_type">
            {{ element.equipment_type }}</td>
          <td @dblclick="onclick_" @blur="onblur_" @input="onInput" data-param-name="manufacturer">{{
            element.manufacturer
          }}
          </td>
          <td @dblclick="onclick_" @blur="onblur_" @input="onInput" data-param-name="name">{{ element.name }}</td>
          <td @dblclick="onclick_" @blur="onblur_" @input="onInput" data-param-name="price">{{ element.price }}</td>
          <td @dblclick="onclick_" @blur="onblur_" @input="onInput" data-param-name="owner">{{ element.owner }}</td>
          <td @dblclick="onclick_" @blur="onblur_" @input="onInput" data-param-name="belonging">{{ element.belonging }}
          </td>
          <td @dblclick="onclick_" @blur="onblur_" @input="onInput" data-param-name="number">{{ element.number }}</td>
          <td @dblclick="onclick_" @blur="onblur_" @input="onInput" data-param-name="date_in">{{ element.date_in }}</td>
          <td @dblclick="onclick_" @blur="onblur_" @input="onInput" data-param-name="date_out">{{ element.date_out }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import {
  getAPI
} from '../api/axios-base'
import {
  mapState
} from 'vuex'
export default {
  name: 'Downloads',
  components: {
  },
  data () {
    return {
      sort_by: '',
      sort: '',
      search: ''
    }
  },
  methods: {
    async sort_col (event) {
      let paramName = event.target.dataset.paramName
      if (this.sort === '' || this.sort === 'ASC') {
        this.sort = 'DESC'
      } else {
        this.sort = 'ASC'
      }
      this.sort_by = paramName

      let data = new FormData()
      data.append('attribute', paramName)
      data.append('sort', this.sort)

      let request = await fetch('http://127.0.0.1:8000/sorted', {
        method: 'post',
        body: data
      })
      let json = await request.json()
      this.$store.state.elements = json.elements
    },
    onclick_ (event) {
      if (this.isEditable) {
        event.target.setAttribute('contenteditable', 'true')
      }
    },
    onblur_ (event) {
      if (this.isEditable) {
        event.target.setAttribute('contenteditable', 'false')
        let elementId = event.target.parentElement.id
        let paramName = event.target.dataset.paramName
        let newValue = event.target.innerHTML
        this.elements[Number(elementId)][paramName] = newValue

        let data = new FormData()
        data.append('element_id', elementId)
        data.append('attribute', paramName)
        data.append('value', newValue)

        fetch('http://127.0.0.1:8000/save', {
          method: 'post',
          body: data
        })
      }
    },
    onInput (e) {
      console.log(e)
    }
  },
  computed: {
    searchResults () {
      if (this.search === '') {
        return this.elements
      } else {
        let results = {}
        Object.entries(this.elements).forEach(([id, element]) => {
          Object.entries(element).forEach(([key, value]) => {
            console.log(value)
            if (key !== 'id') {
              if (value.indexOf(this.search) !== -1) {
                results[id] = element
              }
            }
          })
        })
        console.log(results)
        return results
      }
    },
    ...mapState(['elements', 'isEditable'])
  }, // get APIData from store.state.
  created () {
    getAPI.get('list', {
      headers: {
        Authorization: `Bearer ${this.$store.state.accessToken}`
      }
    }) // proof that your access token is still valid; if not the
      // axios getAPI response interceptor will attempt to get a new  access token from the server. check out ../api/axios-base.js getAPI instance response interceptor
      .then(response => {
        console.log('GetAPI successfully got the mods')
        this.$store.state.elements = response.data.elements // store the response data in store
      })
      .catch(err => { // refresh token expired or some other error status
        console.log(err)
      })

    getAPI.get('rights', {
      headers: {
        Authorization: `Bearer ${this.$store.state.accessToken}`
      }
    }) // proof that your access token is still valid; if not the
      // axios getAPI response interceptor will attempt to get a new  access token from the server. check out ../api/axios-base.js getAPI instance response interceptor
      .then(response => {
        console.log('GetAPI successfully got the mods')
        this.$store.state.isEditable = response.data.edit_group // store the response data in store
        console.log(this.$store.state.isEditable)
      })
      .catch(err => { // refresh token expired or some other error status
        console.log(err)
      })
  }
}
</script>
